import * as THREE from "three";

const dim = 400;

export default class Particle {
  constructor() {
    const x = dim - Math.random() * 2 * dim;
    const y = dim - Math.random() * 2 * dim;
    const z = dim - Math.random() * 2 * dim;
    this.position = new THREE.Vector3(x, y, z);
    this.velocity = new THREE.Vector3(
      (Math.random() - 0.5) / 3,
      (Math.random() - 0.5) / 3,
      (Math.random() - 0.5) / 3
    );
    this.velocity.clampScalar(0.01, 0.02);
    this.acceleration = new THREE.Vector3(0, 0, 0);
    this.jerk = new THREE.Vector3(0, 0, 0);
    this.textBoundaries = false;
    this.targetPos = new THREE.Vector3(x, y, z);
    this.startPos = new THREE.Vector3(x, y, z);
    this.neighbours = [];
    this.r = 100;
    this.speedLimit = 0.1;
  }

  update() {
    this.jerk.set(
      (Math.random() - 0.5) / 200,
      (Math.random() - 0.5) / 200,
      (Math.random() - 0.5) / 200
    );
    this.acceleration.add(this.jerk);
    this.acceleration.clampScalar(-0.01, 0.01);
    this.velocity.add(this.acceleration);
    this.velocity.clampScalar(-this.speedLimit, this.speedLimit);
    this.position.add(this.velocity);
    this.boundaryCheck();
  }

  boundaryCheck() {
    if (!this.textBoundaries) {
      if (this.position.x < -dim || this.position.x > dim) {
        this.velocity.x *= -1;
        this.acceleration.x *= -1;
      }
      if (this.position.y < -dim || this.position.y > dim) {
        this.velocity.y *= -1;
        this.acceleration.y *= -1;
      }
      if (this.position.z < -dim || this.position.z > dim) {
        this.velocity.z *= -1;
        this.acceleration.z *= -1;
      }
      return;
    }
    if (
      this.position.x < this.targetPos.x - 1 ||
      this.position.x > this.targetPos.x + 1
    ) {
      this.velocity.x *= -1;
      this.acceleration.x *= -1;
    }
    if (
      this.position.y < this.targetPos.y - 1 ||
      this.position.y > this.targetPos.y + 1
    ) {
      this.velocity.y *= -1;
      this.acceleration.y *= -1;
    }
    if (
      this.position.z < this.targetPos.z - 1 ||
      this.position.z > this.targetPos.z + 1
    ) {
      this.velocity.z *= -1;
      this.acceleration.z *= -1;
    }
  }

  interpolate(progress) {
    this.position.lerpVectors(this.startPos, this.targetPos, progress);
  }
}
