import { useRef, useMemo } from "react";
import { Vector4, Object3D, AdditiveBlending } from "three";
import { useTexture } from "@react-three/drei";
import { useFrame } from "@react-three/fiber";
import smokeImage from "./Smoke-Element.png";

const scratchObject3D = new Object3D();

const randn_bm = (min, max) => {
  let u = 0,
    v = 0;
  while (u === 0) u = Math.random(); //Converting [0,1) to (0,1)
  while (v === 0) v = Math.random();
  let num = Math.sqrt(-2.0 * Math.log(u)) * Math.cos(2.0 * Math.PI * v);

  num = num / 10.0 + 0.5; // Translate to 0 -> 1
  if (num > 1 || num < 0) num = randn_bm(min, max);
  // resample between 0 and 1 if out of range
  else {
    num *= max - min; // Stretch to fill range
    num += min; // offset to min
  }
  return num;
};

export default function Smoke({ stage }) {
  const meshRef = useRef();
  const smokeTexture = useTexture(smokeImage);

  const particles = useMemo(() => {
    const particles = [];
    for (let i = 0; i < 150; i++) {
      const x = randn_bm(-5000, 5000);
      // let x = Math.random() * 4000 - 2000;
      const newParticle = new Vector4(
        x,
        Math.cos(x / 1250) * (Math.random() * 1200 - 600),
        Math.random() * 500 - 3000,
        Math.random() * 360
      );
      particles.push(newParticle);
    }
    return particles;
  }, []);

  useFrame(() => {
    if (!meshRef.current) return;
    if (meshRef.current.material.opacity < 0.13) {
      meshRef.current.material.opacity += 0.0001;
    }
    for (let i = 0; i < particles.length; i++) {
      particles[i].w += 0.0015;
      const { x, y, z, w } = particles[i];
      scratchObject3D.position.set(x, y, z);
      scratchObject3D.rotation.set(0, 0, w);
      scratchObject3D.updateMatrix();
      meshRef.current.setMatrixAt(i, scratchObject3D.matrix);
    }
    meshRef.current.instanceMatrix.needsUpdate = true;
  });

  return (
    <>
      {stage > 2 && (
        <>
          <pointLight
            color={0xd8547e}
            intensity={90}
            distance={2200}
            decay={1}
            position={[-400, -100, -2500]}
          />
          <pointLight
            color={0xcc6600}
            intensity={50}
            distance={2200}
            decay={1}
            position={[0, 0, -2500]}
          />
          <pointLight
            color={0x3677ac}
            intensity={50}
            distance={2200}
            decay={1}
            position={[1200, -100, -2500]}
          />
          <instancedMesh
            ref={meshRef}
            args={[null, null, 150]}
            frustumCulled={false}
            renderOrder={1}
          >
            <planeBufferGeometry args={[900, 900]} />
            <meshStandardMaterial
              color="rgb(175, 154, 115)"
              map={smokeTexture}
              transparent={true}
              opacity={0}
              depthWrite={false}
              blending={AdditiveBlending}
            />
          </instancedMesh>
        </>
      )}
    </>
  );
}
