import { useEffect, useState } from "react";
import { isBrowser } from "react-device-detect";
import { useFrame } from "@react-three/fiber";
import { Text } from "@react-three/drei";

import Connections from "./Components/Connections";
import useParticles from "./Components/useParticles";
import Sphere from "./Octree/Sphere";
import * as d3 from "d3-ease";
const range = new Sphere();

const textRange = isBrowser ? 10 : 5;
export default function Particles({ stage, setStage, mouseRange, progress }) {
  const [showText, setShowText] = useState(true);
  const particles = useParticles();

  useEffect(() => {
    if (stage === 0) return;
    progress.current = 0;
    particles.forEach((particle) => {
      particle.startPos.set(...Object.values(particle.position));
    });

    if (stage === 2) {
      particles.forEach((particle) => {
        particle.targetPos.set(
          600 - Math.random() * 1200,
          400,
          -Math.random() * 400
        );
      });
      return;
    }
  }, [stage, particles, progress]);

  useFrame(() => {
    if (progress.current > 1) {
      particles.forEach((particle) => particle.update());
      return;
    }
    progress.current += 0.01;

    //STAGE 1: FORM TEXT
    if (stage === 1) {
      mouseRange.current += 0.1;
      const easing = d3.easeSinOut(progress.current);
      particles.forEach((particle) => particle.interpolate(easing));
      range.r = (1 - easing) * 150 + easing * textRange;
      range.rSquared = range.r * range.r;
      if (progress.current > 1) {
        particles.forEach((particle) => {
          particle.textBoundaries = true;
          particle.speedLimit = 0.03;
        });
      }
      return;
    }
    //STAGE 2: SCATTER
    if (stage === 2) {
      mouseRange.current -= 0.1;
      const easing = d3.easeCubicIn(progress.current);
      particles.forEach((particle) => particle.interpolate(easing));
      range.r = (1 - easing) * textRange + easing * 50;
      range.rSquared = range.r * range.r;
      if (progress.current > 0.55) setShowText(false);
      if (progress.current > 1) {
        setStage(3);
      }
      return;
    }
  });

  return (
    <>
      <Connections particles={particles} range={range} />
      {showText && (
        <Text
          fontSize={20}
          position={[0, 100, 0]}
          color="#5A1525"
          anchorX="center"
          anchorY="middle"
        >
          David Beale
        </Text>
      )}
    </>
  );
}
