import { useCallback, useRef } from "react";
import { isBrowser } from "react-device-detect";

const clamp = (value, min, max) => {
  if (value < min) return min;
  if (value > max) return max;
  return value;
};

const maxY = isBrowser ? 1850 : 3600;
const minY = 0;

//
// ─── MOUSE ──────────────────────────────────────────────────────────────────────
//

export const useMouseInteraction = (
  progress,
  debug,
  setStage,
  scroll,
  mouse
) => {
  const ignore = useRef(false);

  const onMouseMove = useCallback(
    (e) => {
      mouse.current = [
        2500 * (e.clientX / window.innerWidth - 0.5),
        1500 * (e.clientY / window.innerHeight - 0.5),
      ];
    },
    [mouse]
  );

  const onScroll = useCallback(
    (e) => {
      if (progress.current < 1 || ignore.current) return;

      if (debug) {
        scroll.current = clamp(scroll.current + e.deltaY * 0.8, minY, maxY);
        return;
      }
      setStage((prevStage) => {
        if (prevStage > 2) {
          scroll.current = clamp(scroll.current + e.deltaY * 0.8, minY, maxY);
          return scroll.current >= maxY ? Infinity : 3;
        }
        ignore.current = true;
        setTimeout(() => {
          ignore.current = false;
        }, 50);
        return prevStage + 1;
      });
    },
    [debug, progress, scroll, setStage]
  );

  const onClick = useCallback(
    (e) => {
      if (progress.current < 1 || ignore.current) return;

      setStage((prevStage) => {
        if (prevStage <= 2) {
          ignore.current = true;
          setTimeout(() => {
            ignore.current = false;
          }, 50);
          return prevStage + 1;
        }
        return prevStage;
      });
    },
    [setStage, progress]
  );

  return [onClick, onScroll, onMouseMove];
};

//
// ─── TOUCH ──────────────────────────────────────────────────────────────────────
//

export const useTouchInteraction = (
  progress,
  debug,
  setStage,
  scroll,
  swipingInProgress
) => {
  const ignore = useRef(false);

  const prevScroll = useRef(0);
  const onSwipeStart = useCallback(() => {
    prevScroll.current = scroll.current;
    swipingInProgress.current = true;
  }, [scroll, swipingInProgress]);

  const onSwipeStop = useCallback(() => {
    prevScroll.current = scroll.current;
    swipingInProgress.current = false;
  }, [scroll, swipingInProgress]);

  const onSwiping = useCallback(
    (e) => {
      if (progress.current < 1 || ignore.current) return;

      if (debug) {
        scroll.current = clamp(prevScroll.current - e.deltaY, minY, maxY);
        return;
      }
      setStage((prevStage) => {
        if (prevStage > 2) {
          scroll.current = clamp(prevScroll.current - e.deltaY, minY, maxY);
          return scroll.current >= maxY ? Infinity : 3;
        }
        ignore.current = true;
        setTimeout(() => {
          ignore.current = false;
        }, 50);
        return prevStage + 1;
      });
    },
    [debug, progress, scroll, setStage]
  );

  return [onSwipeStart, onSwipeStop, onSwiping];
};
