export default function GithubLogo({ dimensions, github }) {
  const onClick = (e) => {
    e.stopPropagation();
    window.open(github.url, "_blank");
  };

  return (
    <mesh
      onPointerDown={onClick}
      position={[dimensions.w2 - 18, dimensions.h2 - 18, dimensions.depth + 1]}
    >
      <circleBufferGeometry args={[16, 16]} />
      <meshBasicMaterial map={github.logo} />
    </mesh>
  );
}
