import { useLoader } from "@react-three/fiber";
import { isBrowser } from "react-device-detect";
import { useMemo } from "react";
import * as THREE from "three";
import Particle from "../Octree/Particle";
import fontFile from "./font.blob";

const textSize = isBrowser ? 40 : 20;

function fillWithPoints(geometry, count) {
  const dummyTarget = new THREE.Vector3();
  const vA = new THREE.Vector3();
  const vB = new THREE.Vector3();
  const vC = new THREE.Vector3();
  const ray = new THREE.Ray();
  geometry.computeBoundingBox();
  let bbox = geometry.boundingBox;

  const points = [];

  const dir = new THREE.Vector3(1, 1, 1).normalize();

  const halfwayX = (bbox.max.x + bbox.min.x) / 2;
  const halfwayY = (bbox.max.y + bbox.min.y) / 2;
  const halfwayZ = (bbox.max.z + bbox.min.z) / 2;
  let counter = 0;
  while (counter < count) {
    let v = new THREE.Vector3(
      THREE.Math.randFloat(bbox.min.x, bbox.max.x),
      THREE.Math.randFloat(bbox.min.y, bbox.max.y),
      THREE.Math.randFloat(bbox.min.z, bbox.max.z)
    );
    if (isInside(v)) {
      v.x -= halfwayX;
      v.y -= halfwayY;
      v.z -= halfwayZ - 350;
      points.push(v);
      counter++;
    }
  }

  function isInside(v) {
    ray.set(v, dir);
    let counter = 0;

    const pos = geometry.attributes.position;
    const faces = pos.count / 3;

    for (let i = 0; i < faces; i++) {
      vA.fromBufferAttribute(pos, i * 3 + 0);
      vB.fromBufferAttribute(pos, i * 3 + 1);
      vC.fromBufferAttribute(pos, i * 3 + 2);
      if (ray.intersectTriangle(vA, vB, vC, false, dummyTarget)) counter++;
    }

    return counter % 2 === 1;
  }
  return points;
}

function generateText(font) {
  if (!font) return;
  const geometry = new THREE.TextBufferGeometry("welcome", {
    font,
    size: textSize,
    height: 10,
    curveSegments: 3,
  });
  const points = fillWithPoints(geometry, Math.cbrt(window.innerWidth) * 80);
  return points;
}
export default function useTextPoints() {
  const font = useLoader(THREE.FontLoader, fontFile);

  const particles = useMemo(() => {
    const textPoints = generateText(font);
    return textPoints.map((point) => {
      const newParticle = new Particle();
      newParticle.targetPos.set(...Object.values(point));
      return newParticle;
    });
  }, [font]);

  return particles;
}
