import Ring from "./Ring";
import MagicBox from "./MagicBox";
import Image from "./Image";
import Link from "./Link";
import Text from "./Text";
import GithubLogo from "./GithubLogo";

export default function Portal({
  dimensions,
  image,
  setHovered,
  github,
  debug,
  swipingInProgress,
}) {
  return (
    <group
      position={[dimensions.xOffset, dimensions.yOffset, dimensions.zOffset]}
    >
      <Image image={image} dimensions={dimensions} />
      <MagicBox dimensions={dimensions} debug={debug} />
      <Ring dimensions={dimensions} />
      <GithubLogo dimensions={dimensions} github={github} />
      <Link
        dimensions={dimensions}
        setHovered={setHovered}
        image={image}
        swipingInProgress={swipingInProgress}
      />
      <Text dimensions={dimensions} image={image} />
    </group>
  );
}
