import "./Container.css";
import { isBrowser } from "react-device-detect";

import Browser from "./Browser";
import Mobile from "./Mobile";

export default function Container({
  children,
  scroll,
  mouse,
  progress,
  debug,
  setStage,
  swipingInProgress,
}) {
  return (
    <>
      {isBrowser ? (
        <Browser
          scroll={scroll}
          mouse={mouse}
          progress={progress}
          debug={debug}
          setStage={setStage}
        >
          {children}
        </Browser>
      ) : (
        <Mobile
          scroll={scroll}
          progress={progress}
          debug={debug}
          setStage={setStage}
          swipingInProgress={swipingInProgress}
        >
          {children}
        </Mobile>
      )}
    </>
  );
}
