import Portal from "./Portal/Portal";
import { useFrame } from "@react-three/fiber";
import { useEffect, useRef, useState } from "react";
import { useContentData } from "./Portal/data/useContentData";

export default function Content({ scroll, stage, debug, swipingInProgress }) {
  const ref = useRef();
  const [hovered, setHovered] = useState(false);
  const contentData = useContentData();

  useFrame(() => {
    if (!ref.current) return;
    const diff = scroll.current - ref.current.position.y;
    ref.current.position.y += diff * 0.05;
  });

  useEffect(() => {
    document.body.style.cursor = hovered ? "pointer" : "auto";
  }, [hovered]);

  return (
    <>
      {(debug || stage > 2) && (
        <group ref={ref} position={[0, -500, 0]}>
          {contentData.map((data, index) => (
            <Portal
              key={index}
              dimensions={data.dimensions}
              image={data.image}
              github={data.github}
              setHovered={setHovered}
              debug={debug}
              swipingInProgress={swipingInProgress}
            />
          ))}
        </group>
      )}
    </>
  );
}
