import * as THREE from "three";
export default class Sphere {
  constructor(x = 0, y = 0, z = 0, r = 150) {
    this.position = new THREE.Vector3(x, y, z);
    this.r = r;
    this.rSquared = r ** 2;
  }
  set(particle) {
    this.position = particle.position;
    const { x, y, z } = this.position;
    this.left = x - this.r;
    this.right = x + this.r;
    this.top = y + this.r;
    this.bottom = y - this.r;
    this.front = z + this.r;
    this.back = z - this.r;
  }
  contains(particle) {
    if (this.position === particle.position) return false;
    const distSquared = this.position.distanceToSquared(particle.position);
    return distSquared <= this.rSquared;
  }
}
