export default function Image({ image, dimensions }) {
  return (
    <mesh
      renderOrder={5}
      position={[dimensions.backXOffset, 0, -dimensions.depth]}
    >
      <planeBufferGeometry args={[dimensions.w1 * 2, dimensions.h1 * 2]} />
      <meshBasicMaterial transparent={true} map={image.image} />
    </mesh>
  );
}
