import { useEffect, useMemo, useRef } from "react";

export default function Ring({ dimensions }) {
  const ref = useRef();

  const [positions, normals] = useMemo(() => {
    const { w2, h2, depth } = dimensions;
    const thickness = 1;
    const vertices = [
      { pos: [-w2 - thickness, h2 + thickness, depth], norm: [0, 0, 1] },
      { pos: [w2 + thickness, h2 + thickness, depth], norm: [0, 0, 1] },
      { pos: [-w2, h2, depth], norm: [0, 0, 1] },
      { pos: [w2, h2, depth], norm: [0, 0, 1] },

      { pos: [-w2, -h2, depth], norm: [0, 0, 1] },
      { pos: [w2, -h2, depth], norm: [0, 0, 1] },
      { pos: [-w2 - thickness, -h2 - thickness, depth], norm: [0, 0, 1] },
      { pos: [w2 + thickness, -h2 - thickness, depth], norm: [0, 0, 1] },
    ];
    const numVertices = vertices.length;
    const positions = new Float32Array(numVertices * 3);
    const normals = new Float32Array(numVertices * 3);
    let posNdx = 0;
    let nrmNdx = 0;
    for (const vertex of vertices) {
      positions.set(vertex.pos, posNdx);
      normals.set(vertex.norm, nrmNdx);
      posNdx += 3;
      nrmNdx += 3;
    }
    return [positions, normals];
  }, [dimensions]);

  useEffect(() => {
    const index = [
      1, 3, 2, 7, 5, 6, 6, 5, 4, 6, 2, 0, 2, 6, 4, 3, 1, 2, 0, 2, 1, 1, 3, 7, 3,
      5, 7,
    ];
    ref.current.setIndex(index);
  }, []);

  return (
    <mesh scale={[1.01, 1.01, 1.01]}>
      <bufferGeometry ref={ref}>
        <bufferAttribute
          attachObject={["attributes", "position"]}
          args={[positions, 3]}
        />
        <bufferAttribute
          attachObject={["attributes", "normal"]}
          args={[normals, 3]}
        />
      </bufferGeometry>

      <meshBasicMaterial color={"white"} transparent={true} />
    </mesh>
  );
}
