import * as THREE from "three";
import { useEffect, useRef, memo } from "react";
import { useFrame } from "@react-three/fiber";
import Octree from "../Octree/Octree";

const octree = new Octree();
const maxConnections = 30;
export default memo(function Connections({ particles, range }) {
  const geometryRef = useRef();
  const positionRef = useRef();
  const colorRef = useRef();
  const linePositions = useRef(new Float32Array(1000 * maxConnections * 3));
  const lineColors = useRef(new Float32Array(1000 * maxConnections * 3));

  useEffect(() => {
    const r = 0;
    const g = 0.025;
    const b = 0.036;
    for (let i = 0; i < lineColors.current.length; i += 6) {
      lineColors.current[i] = r;
      lineColors.current[i + 1] = g;
      lineColors.current[i + 2] = b;

      lineColors.current[i + 3] = 0;
      lineColors.current[i + 4] = g;
      lineColors.current[i + 5] = b;
    }
    colorRef.current.needsUpdate = true;
  }, []);

  useFrame(() => {
    if (!geometryRef.current) return;

    octree.clear();
    particles.forEach((particle) => octree.insert(particle));

    let vertexPos = 0;
    for (let i = 0; i < particles.length; ++i) {
      const particleA = particles[i];
      range.set(particleA);

      const otherParticles = octree.query(range);
      const numConnections = Math.min(otherParticles.length, maxConnections);
      for (let j = 0; j < numConnections; j++) {
        const particleB = otherParticles[j];

        linePositions.current[vertexPos++] = particleA.position.x;
        linePositions.current[vertexPos++] = particleA.position.y;
        linePositions.current[vertexPos++] = particleA.position.z;

        linePositions.current[vertexPos++] = particleB.position.x;
        linePositions.current[vertexPos++] = particleB.position.y;
        linePositions.current[vertexPos++] = particleB.position.z;
      }
    }
    geometryRef.current.setDrawRange(0, vertexPos / 3);
    positionRef.current.needsUpdate = true;
  });

  return (
    <lineSegments frustumCulled={false}>
      <bufferGeometry
        ref={geometryRef}
        attach="geometry"
        drawRange={{ start: 0, count: 0 }}
      >
        <bufferAttribute
          ref={positionRef}
          attachObject={["attributes", "position"]}
          args={[linePositions.current, 3]}
          usage={THREE.DynamicDrawUsage}
        />
        <bufferAttribute
          ref={colorRef}
          attachObject={["attributes", "color"]}
          args={[lineColors.current, 3]}
          usage={THREE.DynamicDrawUsage}
        />
      </bufferGeometry>
      <lineBasicMaterial
        attach="material"
        vertexColors={true}
        blending={THREE.AdditiveBlending}
      />
    </lineSegments>
  );
});
