import { Text } from "@react-three/drei";

export default function TextComponent({ dimensions, image }) {
  return (
    <Text
      position={[0, -dimensions.h2 + 20, dimensions.depth + 10]}
      fontSize={25}
      color="white"
      outlineColor="#010122"
      outlineWidth={1}
      outlineBlur={5}
      anchorX="center"
      anchorY="middle"
    >
      {image.text}
    </Text>
  );
}
