import Box from "./Box";

export default class Octree {
  constructor(boundary = new Box(0, 0, 0, 400, 400, 400)) {
    this.sectionsLength = 8;
    this.subSections = [];
    this.boundary = boundary;
    this.capacity = 4;
    this.points = [];
  }
  clear() {
    this.subSections = [];
    this.points = [];
  }
  insert(point) {
    if (!this.boundary.contains(point)) return false;
    if (this.points.length < this.capacity) {
      this.points.push(point);
      return true;
    }
    if (!this.subSections.length) {
      this.subdivide();
    }

    for (let section of this.subSections) {
      const success = section.insert(point);
      if (success) return true;
    }
  }

  subdivide() {
    let { x, y, z, w, h, d } = this.boundary;
    x -= w / 2;
    y -= h / 2;
    z -= d / 2;
    for (let i = 0; i < this.sectionsLength; i++) {
      const newBox = new Box(
        x + (i % 2) * w,
        y + Math.floor((i % 4) / 2) * h,
        z + Math.floor(i / 4) * d,
        w / 2,
        h / 2,
        d / 2
      );
      const newTree = new Octree(newBox);
      this.subSections.push(newTree);
    }
  }

  query(range, found = []) {
    if (!this.boundary.intersects(range)) return found;
    this.points.forEach((point) => {
      if (range.contains(point)) found.push(point);
    });

    this.subSections.forEach((section) => {
      section.query(range, found);
    });
    return found;
  }
}
