import { useMouseInteraction } from "./useInteractions";

export default function Browser({
  children,
  scroll,
  mouse,
  progress,
  debug,
  setStage,
}) {
  const [onClick, onScroll, onMouseMove] = useMouseInteraction(
    progress,
    debug,
    setStage,
    scroll,
    mouse
  );
  return (
    <div
      className="container"
      onPointerUp={onClick}
      onWheel={onScroll}
      onPointerMove={onMouseMove}
    >
      <div className="info">*Chrome recommended*</div>
      {children}
    </div>
  );
}
