import { useRef, useState, Suspense } from "react";
import { Canvas } from "@react-three/fiber";
import { OrbitControls, Stats, Loader } from "@react-three/drei";

import Particles from "./Components/Particles/Particles";
import ScrollDown from "./Components/ScrollDown/ScrollDown";
import Sky from "./Components/Sky/Sky";
import Content from "./Components/Content/Content";
import Effects from "./Components/Effects/Effects";
import NoEffects from "./Components/NoEffects/NoEffects";
import Smoke from "./Components/Smoke/Smoke";
import Rig from "./Rig";
import Container from "./Components/Container/Container";
import { isBrowser } from "react-device-detect";

const debug = false;

export default function App() {
  const progress = useRef(2);
  const mouse = useRef([0, 0]);
  const mouseRange = useRef(5);
  const scroll = useRef(0);
  const swipingInProgress = useRef(false);
  const [stage, setStage] = useState(0);

  return (
    <Container
      scroll={scroll}
      mouse={mouse}
      progress={progress}
      debug={debug}
      swipingInProgress={swipingInProgress}
      setStage={setStage}
    >
      <Canvas camera={{ position: [0, 0, 700], fov: 40, far: 10000 }}>
        <Suspense fallback={null}>
          <Stats />
          {!debug && stage <= 2 && (
            <>
              <Effects>
                <Particles
                  stage={stage}
                  setStage={setStage}
                  mouseRange={mouseRange}
                  progress={progress}
                />
              </Effects>
            </>
          )}
          <NoEffects>
            <Content
              scroll={scroll}
              stage={stage}
              debug={debug}
              swipingInProgress={swipingInProgress}
            />
            <Sky />
            <Smoke stage={stage} />
          </NoEffects>
          {/* <OrbitControls /> */}
          {isBrowser && <Rig mouse={mouse} mouseRange={mouseRange} />}
        </Suspense>
      </Canvas>
      <Loader />
      {stage < Infinity && <ScrollDown />}
    </Container>
  );
}
