import { useEffect, useMemo, useRef } from "react";

export default function MagicBox({ dimensions, debug }) {
  const ref = useRef();

  const [positions, normals] = useMemo(() => {
    const { w1, w2, h2, depth, backXOffset, h1 } = dimensions;
    const vertices = [
      // right
      { pos: [w2, -h2, depth], norm: [1, 0, 0] },
      { pos: [w1 + backXOffset, -h1, -depth], norm: [1, 0, 0] },
      { pos: [w2, h2, depth], norm: [1, 0, 0] },
      { pos: [w1 + backXOffset, h1, -depth], norm: [1, 0, 0] },
      // back
      { pos: [w1 + backXOffset, -h1, -depth], norm: [0, 0, -1] },
      { pos: [-w1 + backXOffset, -h1, -depth], norm: [0, 0, -1] },
      { pos: [w1 + backXOffset, h1, -depth], norm: [0, 0, -1] },
      { pos: [-w1 + backXOffset, h1, -depth], norm: [0, 0, -1] },
      // left
      { pos: [-w1 + backXOffset, -h1, -depth], norm: [-1, 0, 0] },
      { pos: [-w2, -h2, depth], norm: [-1, 0, 0] },
      { pos: [-w1 + backXOffset, h1, -depth], norm: [-1, 0, 0] },
      { pos: [-w2, h2, depth], norm: [-1, 0, 0] },
      // top
      { pos: [w1 + backXOffset, h1, -depth], norm: [0, 1, 0] },
      { pos: [-w1 + backXOffset, h1, -depth], norm: [0, 1, 0] },
      { pos: [w2, h2, depth], norm: [0, 1, 0] },
      { pos: [-w2, h2, depth], norm: [0, 1, 0] },
      // bottom
      { pos: [w2, -h2, depth], norm: [0, -1, 0] },
      { pos: [-w2, -h2, depth], norm: [0, -1, 0] },
      { pos: [w1 + backXOffset, -h1, -depth], norm: [0, -1, 0] },
      { pos: [-w1 + backXOffset, -h1, -depth], norm: [0, -1, 0] },
    ];
    const numVertices = vertices.length;
    const positions = new Float32Array(numVertices * 3);
    const normals = new Float32Array(numVertices * 3);
    let posNdx = 0;
    let nrmNdx = 0;
    for (const vertex of vertices) {
      positions.set(vertex.pos, posNdx);
      normals.set(vertex.norm, nrmNdx);
      posNdx += 3;
      nrmNdx += 3;
    }
    return [positions, normals];
  }, [dimensions]);

  useEffect(() => {
    const index = [
      0, 1, 2, 2, 1, 3, 4, 5, 6, 6, 5, 7, 8, 9, 10, 10, 9, 11, 12, 13, 14, 14,
      13, 15, 16, 17, 18, 18, 17, 19,
    ];
    ref.current.setIndex(index);
  }, []);
  return (
    <mesh position={[0, 0, 0]} renderOrder={3} scale={[1.01, 1.01, 1.01]}>
      <bufferGeometry ref={ref}>
        <bufferAttribute
          attachObject={["attributes", "position"]}
          args={[positions, 3]}
        />
        <bufferAttribute
          attachObject={["attributes", "normal"]}
          args={[normals, 3]}
        />
      </bufferGeometry>
      <meshBasicMaterial
        colorWrite={debug ? true : false}
        transparent={true}
        color={debug ? "pink" : ""}
      />
    </mesh>
  );
}
